export default {
  notEmpty(e) { return ((e !== null) && (`${e}`.trim() !== '')) ? true : '   ' },
  length(from, to) { return e => ((e.length >= from) && (e.length <= to)) ? true : ' ' },
  email(e) { return /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i.test(e) ? true : ' Email' },
  notNegative(e) { return parseInt(e) >= 0 ? true : "   " }
}


